import { BaseApiUrl, basicDataBaseApiUrl } from '../../constants/baseConfig';


const WebApiConfig = {
    org: {
        Get: BaseApiUrl + "Organization/sons/",//
        Base: BaseApiUrl + 'Organization',//(post),put
        permissionOrg: BaseApiUrl + 'Permission/'//
    },
    user: {
        Base: BaseApiUrl + 'user',//
        List: BaseApiUrl + "user/list",//
        Priv: basicDataBaseApiUrl + 'dictionarydefines/position',//
        Delete: BaseApiUrl + 'user/delete',
        UserRoleAdd: BaseApiUrl + 'UserRoles/AddToRoles/',//
        UserRoleRemove: BaseApiUrl + 'UserRoles/RemoveFromRoles/',//
        ResetPwd: BaseApiUrl + "user/initpassword",//
    },
    application: {
        Base: BaseApiUrl + 'Application',//
        List: BaseApiUrl + 'Application/list',//
    },
    privilege: {
        Base: BaseApiUrl + 'PermissionItems',
        List: BaseApiUrl + 'PermissionItems/list/',//
        Delete: BaseApiUrl + 'PermissionItems/delete',
    },
    rolePrivilege: {
        Base: BaseApiUrl + 'RolePermissions',//
        Application: BaseApiUrl + 'RoleApplications/'//
    },
    role: {
        Base: BaseApiUrl + 'Roleinfo',
        List: BaseApiUrl + 'Roleinfo/list',
    },
    dic: {
        ParList: basicDataBaseApiUrl + 'dictionarydefines/list',//  get
        AreaList: basicDataBaseApiUrl + "areadefines/list"//
    },

}

export default WebApiConfig;


